@font-face {
  font-family: "social";
  src: url("../assets/fonts/myicons.eot");
  src: url("../assets/fonts/myicons.?#iefix") format("embedded-opentype"), url("../assets/fonts/myicons.woff") format("woff"), url("../assets/fonts/myicons.ttf") format("truetype"), url("../assets/fonts/myicons.#myicons") format("svg");
  font-weight: normal;
  font-style: normal; }

.icon {
  font-family: "social"; }
  .icon:before {
    /* Some common icon stuff */ }
  .icon.icon--menu-button:before {
    content: ""; }

* {
  box-sizing: border-box; }

html {
  font-family: "Arial"; }

body {
  background-color: #fbfbfb; }

li {
  list-style: none; }

.link {
  font-size: 14px;
  color: #5f5d67;
  line-height: 2.143;
  text-decoration: none;
  transition: 0.3s; }
  .link:hover {
    color: #8696cc; }

.title {
  font-size: 24px;
  font-family: "Times New Roman";
  color: #555555;
  line-height: 0.75;
  margin: 0 0 24px 0;
  font-weight: normal; }
  .title--secondary {
    font-size: 24px;
    font-family: "Georgia";
    color: #333333; }

.container {
  max-width: 755px;
  margin: auto; }

.content-wrapper {
  margin: auto;
  max-width: 755px;
  margin-top: 8px; }

.header .container {
  text-align: center;
  position: relative; }
  .header .container .logo {
    text-decoration: none;
    display: block;
    max-width: 400px;
    margin: auto; }
    .header .container .logo img {
      width: 100%; }
  .header .container .button {
    background: none;
    border: none;
    z-index: 99;
    cursor: pointer;
    padding: 0 15px;
    transition: 0.3s;
    outline: none;
    display: none; }
    .header .container .button .icon {
      font-size: 30px; }
    .header .container .button:hover {
      color: #8696cc; }
  .header .container .menu {
