    margin: 6px 0 0 0;
    font-size: 0;
    background-color: #fff; }
    .header .container .menu__item {
      display: inline-block;
      border: 1px solid #999;
      position: relative;
      border-bottom: 3px solid #9dc5f0; }
      .header .container .menu__item:not(:last-child) {
        border-right: 0; }
      .header .container .menu__item .link {
        display: block;
        padding: 0 29.8px; }
      .header .container .menu__item .drop-list {
        position: absolute;
        opacity: 0;
        left: -9999px;
        transition: 0.3s opacity;
        border: 1px solid #8696cc;
        box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.37);
        padding: 0;
        background-color: #fff;
        z-index: 2;
        width: 100%; }
        .header .container .menu__item .drop-list__item {
          padding: 0 16px; }
          .header .container .menu__item .drop-list__item .link {
            padding: 0;
            text-align: left; }
          .header .container .menu__item .drop-list__item:not(:last-child) .link {
            border-bottom: 1px solid #ebf3fc; }
      .header .container .menu__item:hover {
        box-shadow: inset 0px -1px #9dc5f0; }
        .header .container .menu__item:hover .drop-list {
          left: 0;
          top: calc(100% + 3px);
          opacity: 1; }

.main .container .slider {
  width: 755px; }
  .main .container .slider__item {
    width: 755px;
    margin: 0; }
    .main .container .slider__item img {
      width: 755px;
      height: 317px; }

.main .container .menu {
  padding: 0 0 0 70px;
  margin: 0;
  border-top: 3px solid #9dc5f0;
  background-color: #fff; }
  .main .container .menu__item {
    display: inline-block;
    transition: 0.3s; }
    .main .container .menu__item:not(:last-child) {
      margin-right: 50px; }
    .main .container .menu__item:hover {
      box-shadow: inset 0px 1px #9dc5f0; }

.section {
  display: inline-block;
  max-width: 480px;
  background-color: #fff; }
  .section .container {
    padding: 34px 30px 70px 30px;
    font-size: 12px; }
    .section .container .description__item {
      line-height: 1.4; }
      .section .container .description__item:nth-child(2) {
        margin-top: 20px; }
      .section .container .description__item:nth-child(3) {
        margin-top: 26px; }
      .section .container .description__item:nth-child(4) {
        margin-top: 38px; }

.aside {
  display: inline-block;
  max-width: 264px;
  vertical-align: top;
  background-color: #fff;
  margin-left: 6px; }
  .aside .container {
    padding: 34px 12px 180px 30px; }
    .aside .container .description {
      font-size: 12px;
      font-weight: bold;
      line-height: 1.5; }
      .aside .container .description__item:nth-child(2) {
        margin-top: 16px; }

.footer .container {
  margin-top: 14px; }
  .footer .container .copyright {
    font-size: 12px;
    color: rgba(95, 93, 103, 0.502);
    line-height: 1.5;
    text-align: left; }

@media screen and (max-width: 778px) {
  .header .container .button {
    display: inline-block; }
  .header .container .menu {
    display: none; }
    .header .container .menu.active {
      display: block;
      position: absolute;
      z-index: 99;
      left: 50%;
      top: 100%;
      transform: translatex(-50%);
      width: 100%;
      max-width: 200px;
      padding: 15px 20px; }
      .header .container .menu.active .menu__item {
        display: block;
        margin-bottom: 10px;
        border-right: 1px solid #999; }
        .header .container .menu.active .menu__item:hover .drop-list {
          position: relative;
          top: 0;
          left: 0; }
  .main .container .slider {
    max-width: 100%; }
  .main .container .menu {
    padding: 0; }
    .main .container .menu__item {
      display: block;
      width: 100%;
      text-align: center; }
      .main .container .menu__item:hover {
        box-shadow: none; }
  .section {
    max-width: 100%; }
    .section .container {
      padding-bottom: 0; }
  .aside {
    max-width: 100%;
    width: 100%;
    margin: 8px 0 0 0; }
    .aside .container {
      padding-bottom: 0; }
  .footer .container {
    margin-left: 20px; } }
